import React, { Component } from 'react';
import './App.css';
import Movie from './Movie'; // js  (  )
//import LineEllipsis from 'react-lines-ellipsis';

class App extends Component {

  // state
  state = {
  
  };


  componentDidMount(){
      this._getMovies();
  };

  //  
  _renderMovies = () => {
    const movies = this.state.movies.map((movie) => {
      //console.log(movie)
      return  <Movie 
        title={movie.title}
        poster={movie.large_cover_image}
        genres={movie.genres}
        rating={movie.rating}
        synopsis={movie.synopsis}
        key={movie.id} />;
  });
  return movies;
  };

 _getMovies = async () => { // 
  const  movies  = await this._callApi()
  /* _callApi   ( )    */
  this.setState({
     movies
  })
  /* _callApi   ( )    */
}

_callApi= () => {
      /* url 
        promise 
        fetch  () (        )
        .then   
        catch  
      */  
    return fetch('https://yts.am/api/v2/list_movies.json?sort_by=year')
    .then(result => result.json() ) // json()  
    .then(json => json.data.movies)
    .catch(err => console.log(err));
}

  /*    _renderMovies ,      */
    render() {
      return (
        <article className="App">
          {this.state.movies ? this._renderMovies() : <p className="loading">...</p>} 
        </article>
      );
    };
};

export default App;



